<template>
    <div>
        <h2>个人管理中心</h2>
        <template>
            <el-table
                :data="personEdit"
                border
                style="width: 100%">
                <el-table-column
                fixed
                label="日期"
                width="150">
                    <template slot-scope="scope">
                        <span>{{scope.row.date}}</span>
                        <el-input v-model="input1" placeholder="请输入日期" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="测试">
                    <template slot-scope="scope">
                        <span>{{scope.row.name}}</span>
                        <el-input v-model="input2" placeholder="请输入姓名" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="测试">
                    <template slot-scope="scope">
                        <span>{{scope.row.province}}</span>
                        <el-input v-model="input3" placeholder="请输入省份" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="ip地址">
                    <template slot-scope="scope">
                        <span>{{scope.row.ip}}</span>
                        <el-input v-model="input4" placeholder="请输入ip" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="电话">
                    <template slot-scope="scope">
                        <span>{{scope.row.phone}}</span>
                        <el-input v-model="input5" placeholder="请输入电话" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                label="测试">
                    <template slot-scope="scope">
                        <span>{{scope.row.zip}}</span>
                        <el-input v-model="input6" placeholder="请输入邮编" v-show="isInputShow"></el-input>
                    </template>
                </el-table-column>
                <el-table-column  fixed="right"  label="操作1"  width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleEdit(scope.$index,scope.row)" type="text" size="small">{{edits}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column  fixed="right"  label="操作2"  width="100">
                    <template slot-scope="scope">
                        <el-button @click="handleSaves(scope.$index,scope.row)" type="text" size="small">保存</el-button>
                    </template>
                </el-table-column>
                <!-- 修改密码 -->
                <el-table-column  fixed="right"  label="操作3"  width="100">
                    <template slot-scope="scope">
                        <el-button @click="changePsw" type="text" size="small">修改密码</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </template>
        <div id="perCenDoc" style="display:none">
            <el-button @click="elbut1">上传报表</el-button>
            <el-button @click="elbut2">下载报表</el-button>
            <div>
                <!-- 上传文件 -->
                <div v-show="isShow1" id="uploadDoc">
                    <el-upload
                    class="upload-demo"
                    accept=".csv"
                    ref="upload"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    multiple
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :auto-upload="false">
                        <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                        <el-button style="margin-left: 10px;" size="small" type="primary" @click="submitUpload">上传到服务器</el-button>
                        <div slot="tip" class="el-upload__tip">只能上传csv文件，且不超过500kb</div>
                    </el-upload>
                </div>
                <!-- 下载文件 -->
                <div v-show="isShow2" id="downloadDoc">
                    <template>
                        <!-- 选择策略 -->
                        <template>
                            <div>
                                <span class="choiceStrategy">请选择策略类型：</span>
                                <el-select v-model="value" placeholder="请选择">
                                    <el-option
                                    v-for="item in optionsPer"
                                    :key="item.value"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </template>
                        <!-- 选择时间范围 -->
                        <div class="block">
                            <span class="demonstration">请选择时间范围：</span>
                            <el-date-picker
                            v-model="valueTimeRange"
                            type="datetimerange"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            align="right"
                            :default-time="['00:00:00', '23:59:59']">
                            </el-date-picker>
                        </div>
                    </template>
                    <el-button type="primary" id="download" name="download" @click="butDown">下载报表</el-button>
                </div>
            </div>
        </div>
        <!-- 修改密码弹出框 -->
        <div id="personCenAlt" v-show="changePswShow">
          <div id="personCenAlt-content">
            <i class="el-icon-close" @click="cencelPerPswX"></i>
            <div>
              <span>请输入当前密码</span>
              <el-input type="password" v-model="personCenNowPsw" placeholder="请输入当前密码"></el-input>
            </div>
            <div>
              <span>请输入新密码</span>
              <el-input type="password" v-model="personCenNewPsw" placeholder="请输入新密码"></el-input>
            </div>
            <div>
              <span>请重新输入新密码</span>
              <el-input type="password" v-model="personCenNewPswConfirm" placeholder="请重新输入新密码"></el-input>
            </div>
            <p>{{perpsonPswValue}}</p>
            <div>
              <el-button round @click="cancelPerPsw">取消修改</el-button>
              <el-button type="success" round @click="subPerPsw">确认修改</el-button>
            </div>
          </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import common from "@/utils/common";
    export default {
        name:'personCenter',
        data() {
            return {
                perCustomerID:'',
                perUserID:'',
                perUsername:'',
                edits:'编辑',
                personEdit: [
                    {
                        date: '2016-05-03',
                        name: '王小虎',
                        province: '上海浦东',
                        ip: '192.168.1.11',
                        phone:'13111111111',
                        zip: 110
                    }, 
                ],
                isInputShow:false,//编辑输入框是否出现
                input1:'',
                input2:'',
                input3:'',
                input4:'',
                input5:'',
                input6:'',
                isShow1:false,//上传文档弹出层是否显示
                isShow2:false,//下载文档弹出层是否显示
                optionsPer: [{
                value: 'T0',
                }, {
                value: 'Algo',
                }],//select
                value: '',//select
                valueTimeRange:'',//选择显示的时间值
                perValueTimeStar:'',
                perValueTimeEnd:'',
                pickerOptions:{
                    shortcuts: [
                        {
                            text: '最近一周',
                            onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                            }
                        }, {
                            text: '最近一个月',
                            onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                            }
                        }, {
                            text: '最近三个月',
                            onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                            }
                        }
                    ]
                },
                changePswShow:false,//修改密码弹出框
                personCenNowPsw:'',//当前密码
                personCenNewPsw:'',//新密码
                personCenNewPswConfirm:'',//确认新密码
                perpsonPswValue:'',//错误提示
            }
        },
        methods: {
            // 点击编辑
            handleEdit(index,row) {
                console.log(index,row,row.date);
                if (this.edits == '编辑') {
                    this.edits = '取消编辑';
                    this.input1 = '';
                    this.input2 = '';
                    this.input3 = '';
                    this.input4 = '';
                    this.input5 = '';
                    this.input6 = '';
                    this.isInputShow = true;
                } else if(this.edits == '取消编辑') {
                    this.edits = '编辑';
                    this.isInputShow = false;
                }
            },
            // 点击保存
            handleSaves(index,row) {
                if ((common.isNull(this.input1))&&(common.isNull(this.input2))&&(common.isNull(this.input3))&&(common.isNull(this.input4))&&(common.isNull(this.input5))&&(common.isNull(this.input6))||(this.isInputShow == false)) {
                    common.altPrompt('暂不可保存');
                    return false;
                }else{
                    // for循环清空的初始化信息，然后把修改过后的信息保存替换
                    for (let i = 0; i < this.personEdit.length; i++) {
                        this.personEdit[i].date = this.input1;
                        this.personEdit[i].name = this.input2;
                        this.personEdit[i].province = this.input3;
                        this.personEdit[i].ip = this.input4;
                        this.personEdit[i].phone = this.input5;
                        this.personEdit[i].zip = this.input6;
                    }
                    this.personEdit = [
                        {
                            date: this.input1,
                            name: this.input2,
                            province: this.input3,
                            ip: this.input4,
                            phone: this.input5,
                            zip: this.input6,
                        }, 
                    ],
                    common.altPrompt('保存成功')
                    this.edits = '编辑';
                    this.isInputShow = false;
                }
                console.log(index,row)
            },
            // 修改密码
            changePsw(){
                console.log('点击修改密码按钮')
                this.changePswShow = true;
            },
            // 取消修改密码
            cancelPerPsw(){
                console.log('取消修改密码')
                this.personCenNowPsw = '';
                this.personCenNewPsw = '';
                this.personCenNewPswConfirm = '';
                this.changePswShow = false;
                common.altPrompt('已取消')
            },
            //确认修改密码
            subPerPsw(){
                this.perUserID = common.decipher('wsUserID');
                if (common.isNull(this.personCenNowPsw)) {
                    common.altPrompt('当前密码不能为空')
                    return false
                }
                if (common.isNull(this.personCenNewPsw) || common.isNull(this.personCenNewPswConfirm)) {
                    common.altPrompt('新密码不能为空')
                    return false
                }else if(this.personCenNewPsw != this.personCenNewPswConfirm){
                    common.altPrompt('两次输入密码不一致，请确认后重新输入')
                    return false
                }else if(this.personCenNowPsw == this.personCenNewPsw){
                    common.altPrompt('新密码不能与旧密码相同，请确认后重新输入')
                    return false
                }
                console.log('q确认修改密码'+domain.personUpdatePwd+this.perUserID+'/'+this.personCenNewPsw)
                axios.get(domain.personUpdatePwd+this.perUserID+'/'+this.personCenNewPsw)
                    .then((response) => {
                        console.log(response)
                        if (response.data.responseEntity == 'OK') {
                            common.altPrompt('修改密码成功')
                            this.$router.push({
                                name:'login'
                            })
                        }else{
                            common.altPrompt('修改密码失败')
                        }
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            },
            // 点击x
            cencelPerPswX(){
                this.personCenNowPsw = '';
                this.personCenNewPsw = '';
                this.personCenNewPswConfirm = '';
                this.changePswShow = false;
            },
            // 点击上传上传文档按钮显示上传的弹出层
            elbut1(){
                this.isShow1 = true;
                this.isShow2 = false;
            },
            // 上传文件
            submitUpload() {
                this.$refs.upload.submit();
            },
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            // 点击下载文档按钮显示上传的弹出层
            elbut2(){
                this.isShow2 = true;
                this.isShow1 = false;
            },
            // 下载文件
            butDown(){
                console.log(this.valueTimeRange)
               // let downURL = "https://codeload.github.com/douban/douban-client/legacy.zip/master";
                // window.open(downURL)
                this.perUsername = common.decipher('wsUserName');
                this.perCustomerID = common.decipher('wsCustomerID');
                this.valueTimeRange = common.changeTimeStamp(this.valueTimeRange);
                this.perValueTimeStar = common.changeTime(this.valueTimeRange.valueTimeStar);
                this.perValueTimeEnd = common.changeTime(this.valueTimeRange.valueTimeEnd);
                console.log(domain.perActualCsvFile+'/'+this.perCustomerID+'/'+this.value+'/'+this.perValueTimeStar,'下载文件',this.perCustomerID,this.value,this.perValueTimeStar,this.perValueTimeEnd)
                axios.get(domain.perActualCsvFile+this.perCustomerID+'/'+this.value+'/'+this.perValueTimeStar)
                    .then((response) => {
                        console.log(response)
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            },
        },
    }
</script>

<style>
    #uploadDoc,#downloadDoc{
        padding-top: 25px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .demonstration{
        margin: 0 3px 0 50px;
    }
    #download{
        margin: 20px 0 0 20px;
    }
    .choiceStrategy{
        margin-left: -185px;
    }
</style>



