<template>
    <div>
        <div id="content-index">
            <div id="content-choice">
                <div id="index-fom">
                    <div class="lab-buy">
                        账<span class="sp-ProNmae1">户</span><span class="sp-ProNmae2">名：</span>
                            <!-- 机构列表 -->
                        <el-select v-model="selected1" placeholder="请选择" @change="changePro()">
                            <el-option
                            v-for="option1 in options1"
                            :key="option1.id"
                            :value="option1.clientName">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="lab-buy">
                        策<span class="sp-txt">略：</span>
                        <el-select v-model="selected2" placeholder="请选择策略">
                            <el-option
                            v-for="option2 in options2"
                            :key="option2"
                            :value="option2">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="lab-buy">
                        <span style="margin-right:10px;">委托类型：</span>
                        <el-select v-model="inpOrderType" placeholder="请选择委托类型">
                            <el-option
                            v-for="item in inpOrderTypeOptions"
                            :key="item.inpOrderTypes"
                            :value="item.inpOrderTypes">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="lab-buy">
                        代<span class="sp-txt">码：</span>
                        <el-input v-model="inpSymbol" placeholder="请输入证券代码" maxlength="6"></el-input>
                    </div>
                    <div class="lab-buy">
                        股<span class="sp-txt">数：</span>
                        <el-input v-model="inpOrderQty" placeholder="请输入交易股数"></el-input>
                    </div>
                    <div class="lab-buy">
                        股<span class="sp-txt">价：</span>
                        <el-input v-model="inpPrice" placeholder="请输入整数或两位小数点股价"></el-input>
                    </div>
                    <div class="lab-buy">
                        方<span class="sp-txt">向：</span>
                        <el-radio v-model="pickedRadio1" label="买">买</el-radio>
                        <el-radio v-model="pickedRadio1" label="卖">卖</el-radio>
                    </div>
                    <div class="lab-buy">
                        涨停不卖跌停不买：
                        <el-radio v-model="pickedRadio2" label="是">是</el-radio>
                        <el-radio v-model="pickedRadio2" label="否">否</el-radio>
                    </div>
                    <div class="lab-buy">
                        <span>结束时间后是否继续执行：</span>
                        <el-radio v-model="pickedRadio3" label="是">是</el-radio>
                        <el-radio v-model="pickedRadio3" label="否">否</el-radio>
                    </div>
                    <div>
                        <span>请选择时间范围：</span>
                        <el-date-picker
                        v-model="valueTime"
                        type="datetimerange"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        :default-time="['00:00:00','23:59:59']">
                        </el-date-picker>
                    </div>
                </div>
                <el-button type="primary" id="indexsubOrder" @click="entrustStock">委托</el-button>
            </div>
            <!-- 回报 -->
            <div id="indexPayback">
                <el-table
                :data="indexTableData"
                border
                style="width: 100%; margin-top: 20px">
                <el-table-column  prop="adaptorPwd1"  label="账户编号"></el-table-column>
                <el-table-column  prop="adaptorPwd2"  label="策略编号"></el-table-column>
                <el-table-column  prop="adaptorType"  label="证券代码"></el-table-column>
                <el-table-column  prop="adaptorUser1"  label="委托状态"></el-table-column>
                <el-table-column  prop="adaptorUser2"  label="买/卖"></el-table-column>
                <el-table-column  prop="broker"  label="交易所类型"></el-table-column>
                <el-table-column  prop="clientID"  label="证券类型"></el-table-column>
                <el-table-column  prop="clientName"  label="委托股数"></el-table-column>
                <el-table-column  prop="customerID"  label="委托类型"></el-table-column>
                <el-table-column  prop="id"  label="成交数量"></el-table-column>
                <el-table-column  prop="isCredit"  label="仍可撮合量"></el-table-column>
                <el-table-column fixed="right" width="100"  label="操作">
                    <template slot-scope="scope">
                    <el-button type="danger" @click="indexUndo(scope.row)">撤销</el-button>
                    </template>
                </el-table-column>
                </el-table>
            </div>
            <!-- 分页 -->
            <div class="block">
              <span class="demonstration"></span>
              <el-pagination style="margin:auto"
                background
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="150">
              </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common";
export default {
    // 注入reload方法刷新页面
  inject:['reload'],
  name: "index",
  data: function() {
    return {
      userid: "",
      selected1: "", //账户名
      options1: [],
      indexClientID: "",
      id: "",
      selected2: "", //策略
      options2: [],
      inpSymbol: "", //证券代码
      inpOrderQty: "", //委托股数
      inpPrice: "", //委托股价
      pickedRadio1: "", //买卖方向
      pickedRadio2: "", //涨跌
      pickedRadio3: "", //结束时间后是否执行
      inpOrderTypeOptions:[{inpOrderTypes:'TWAP'},{inpOrderTypes:'VWAP'}],//委托类型
      inpOrderType: "", //委托类型
      valueTime: "", //日历时间
      valueTimeStar: "", //日历开始时间
      valueTimeEnd: "", //日历结束时间
      //   回报
      indexTableData: [],
      datas:'',//回报数据
      currentPage: 1,
      pages:'',//总页数
    };
  },
  methods: {
    // 获取账户名
    showPro: function() {
      this.userid = common.decipher('wsUserID');
      console.log(domain.indFindCustomer + this.userid);
      axios
        .get(domain.indFindCustomer + this.userid)
        .then(response => {
          console.log(response);
          this.options1 = response.data.responseEntity;
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 根据产品名展示策略
    changePro: function() {
      console.log(this.selected1, this.options1);
      //  for循环找出产品名对应clientID
      for (let i = 0; i < this.options1.length; i++) {
        if (this.selected1 == this.options1[i].clientName) {
          this.indexClientID = this.options1[i].clientID;
        }
      }
      console.log(domain.indFindStrategy + this.indexClientID);
      axios
        .get(domain.indFindStrategy + this.indexClientID)
        .then(res => {
          console.log(res);
          this.options2 = [];
          for (let i = 0; i < res.data.responseEntity.length; i++) {
            // console.log(res.data[i].strategy)
            if (res.data.responseEntity[i].strategyID == "2001") {
              res.data.responseEntity[i].strategyID = "T0";
            } else if (res.data.responseEntity[i].strategyID == "2002") {
              res.data.responseEntity[i].strategyID = "Algo";
            }
            this.options2.push(res.data.responseEntity[i].strategyID);
          }
          console.log(this.options2);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 委托生成订单
    entrustStock() {
      // 当前时间
      console.log(
        common.getNowFormatDate(),
        new Date(common.getNowFormatDate()).getTime()
      );
      // 选择时间
      this.valueTime = common.changeTimeStamp(this.valueTime);
      this.valueTimeStar = this.valueTime.valueTimeStar;
      this.valueTimeEnd = this.valueTime.valueTimeEnd;
      
      let choiceTime = new Date(this.valueTimeStar).getTime();
      let nowTime = new Date(common.getNowFormatDate()).getTime();
      console.log(this.valueTimeStar, this.valueTimeEnd, choiceTime, nowTime);
      let inpSymbolReg = /^[0-9]\d*$/; //股票代码正则验证
      // let inpSymbolPriceReg = /^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$/;
      let inpSymbolPriceReg = /^[0-9]+(.[0-9]{2})?$/;//小数点后二位
      let inpSymbolCD1 = this.inpSymbol.substr(0, 1);
      let inpSymbolCD2 = this.inpSymbol.substr(0, 2);
      console.log(inpSymbolCD1, inpSymbolCD2, this.inpOrderQty.substr(0, 1));
      if (common.isNull(this.selected1)) {
        common.altPrompt("请选择产品名");
        return false;
      }
      if (common.isNull(this.selected2)) {
        common.altPrompt("请选择策略");
        return false;
      }
      if (common.isNull(this.inpOrderType)) {
        common.altPrompt("请选择委托类型");
        return false;
      }
      if (
        this.inpSymbol.length == "6" &&
        inpSymbolReg.test(this.inpSymbol) &&
        (inpSymbolCD2 == "00" ||
          inpSymbolCD2 == "30" ||
          inpSymbolCD2 == "15" ||
          inpSymbolCD2 == "13" ||
          inpSymbolCD1 == "2" ||
          inpSymbolCD1 == "5" ||
          inpSymbolCD1 == "6")
      ) {
        console.log("股票代码正确");
      } else {
        common.altPrompt("股票代码错误");
        return false;
      }
      if (
        inpSymbolReg.test(this.inpOrderQty) &&
        this.inpOrderQty.substr(0, 1) != 0 && 
        common.goAliquot(this.inpOrderQty) != false
      ) {
        console.log("股数正确");
      } else {
        common.altPrompt("股数错误");
        return false;
      }
      if (common.isNull(this.inpPrice) || this.inpPrice == 0) {
        common.altPrompt("股价不能为空或零");
        return false;
      } else if (inpSymbolReg.test(this.inpPrice) && this.inpPrice.substr(0, 1) != 0 || inpSymbolPriceReg.test(this.inpPrice)) {
        console.log("股价正确");
      }else {
        common.altPrompt("股价错误");
        return false;
      }
      if (common.isNull(this.pickedRadio1)) {
        common.altPrompt("请选择委托买卖方向");
        return false;
      }
      if (common.isNull(this.pickedRadio2)) {
        common.altPrompt("请选择涨停跌停后是否买卖");
        return false;
      }
      if (common.isNull(this.pickedRadio3)) {
        common.altPrompt("请选择结束时间后是否继续执行");
        return false;
      }
      if (choiceTime < nowTime) {
        common.altPrompt("最晚时间不得早于当天");
        this.valueTime = "";
        return false;
      }
      // 返回一个下单的状态
      console.log(domain.indMd);
      axios
        .post(domain.indMd, {
          userid: this.userid,
          clientID: this.indexClientID, //产品名
          AccountID: this.selected2, //策略
          symbol: this.inpSymbol, //股票代码
          orderQty: this.inpOrderQty, //委托股数
          price: this.inpPrice, //委托价格
          side: this.pickedRadio1, //买卖
          limitAction: this.pickedRadio2, //涨停跌停
          afterAction: this.pickedRadio3, //结束时间后是否继续执行
          ordType: this.inpOrderType, //委托类型
          EffectiveTime: this.valueTimeStar, //生效时间
          ExpireTime: this.valueTimeEnd, //终止时间
        })
        .then(response => {
          console.log(response);
          if (response.data == "200") {
            common.altPrompt("委托成功");
            this.selected1 = "";
            this.selected2 = "";
            this.inpSymbol = "";
            this.inpOrderQty = "";
            this.inpPrice = "";
            this.pickedRadio1 = "";
            this.pickedRadio2 = "";
            this.pickedRadio3 = "";
            this.inpOrderType = "";
            this.valueTimeStar = "";
            this.valueTimeEnd = "";
            this.reload();
          } else if (response.data == "400") {
            common.altPrompt("委托失败");
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 撤销
    indexUndo(row, index) {
      console.log(row, index);
    },
