<template>
    <div id="tabsChoice">
        <ul class="tabs">
            <li class="li-tab" v-for="(item,index) in tabsParam" 
            @click="toggleTabs(index)"
            :class="{active:index!=nowIndex}" :key="item">{{item}}</li>
        </ul>
        <span class="tabsChoice" @click="loginAginEnt">退出系统</span>
        <p style="margin-top:20px;">{{years}}年{{months}}月{{days}}日&nbsp;&nbsp;&nbsp;{{nowTimes}}</p>
        <!-- 个人中心 -->
        <div class="divTab" v-show="nowIndex===0">
            <personCenter></personCenter>
        </div>
        <!-- 算法单委托 -->
        <div class="divTab" v-show="nowIndex===1">
            <index></index>
        </div>
        <!-- 普通单委托 -->
        <div class="divTab" v-show="nowIndex===2">
            <commonOrdinary></commonOrdinary>
        </div>
    </div>
</template>

<script>
import personCenter from "@/page/personCenter/personCenter"
import index from "@/page/index/index";
import commonOrdinary from "@/page/commonOrdinary/commonOrdinary";
export default {
  name: "tabsEntrust",
  data() {
    return {
      tabsParam: ["个人中心","算法单委托", "普通单委托"], //（这个也可以用对象key，value来实现）
      nowIndex: 2, //默认第一个tab为激活状态
      years:'',
      months:'',
      days:'',
      nowTimes:'',
    };
  },
  components: {
    personCenter,
    index,
    commonOrdinary
  },
  mounted:function(){
    setInterval(this.showTime,1000)
  },
  methods: {
    // tab切换
    toggleTabs: function(index) {
      this.nowIndex = index;
    },
    // 显示时间流动
    showTime() {
			let nowtime = new Date();
			let year = nowtime.getFullYear();
			let month = nowtime.getMonth() + 1;
			let date = nowtime.getDate();
      
      this.years = year;
      this.months = month;
      this.days = date;
      this.nowTimes = nowtime.toLocaleTimeString()
    },
    // 跳转首页
    loginAginEnt(){
      this.$router.push({
        name:'login',
        param:''
      })
    },
  }
};
</script>

<style>
.active {
  background: #eee;
}
.tabs {
  font: 0;
  padding: 0;
}
.li-tab {
  width: 33%;
  height: 100%;
  display: inline-block;
  text-align: center;
}
</style>



