<template>
    <div id="comOrdinary">
      <div id="comOrdinary-content">
        <div id="comOrdinaryClientID">
          <span>账<span class="spanname">户</span>名:</span>
          <el-select v-model="comOrdinaryClientName" placeholder="请选择账户名" @change="handleClickcomOrdinary">
            <el-option
              v-for="item in comOrdinaryOptions"
              :key="item.clientName"
              :value="item.clientName">
            </el-option>
          </el-select>
        </div>
        <!-- 证券代码 -->
        <div id="comOrdinarySymbol">
          <span>证券代码:</span>
          <el-input v-model="comOrdinarySymbol" maxlength="6" placeholder="请输入证券代码">六位证券代码</el-input>          
        </div>
        <!-- 委托股数 -->
        <div id="comOrdinaryOrderQty">
          <span>委托股数:</span>
          <el-input v-model="comOrdinaryOrderQty" placeholder="请输入股票数量">委托股票数量</el-input>
        </div>
        <!-- 委托价格 -->
        <div id="comOrdinaryPrice">
          <span>委托价格:</span>
          <el-input v-model="comOrdinaryPrice" placeholder="请输入整数或两位小数点股价">委托股票价格</el-input>
        </div>
        <!-- 买卖方向 -->
        <div id="comOrdinarySide">
          <span class="comOrdinarySideSpan1">方</span><span class="comOrdinarySideSpan2">向:</span>
          <el-radio v-model="comOrdinarySide" label="买">买</el-radio>
          <el-radio v-model="comOrdinarySide" label="卖">卖</el-radio>
        </div>
        <template>
          <el-button type="primary" id="sureOrder" @click="sureOrder">下单</el-button>
        </template>
      </div>
      <!-- 回报 -->
      <div id="comOrdinaryPayback">
        <el-table
          :data="comOrdinaryTableData"
					:span-method="comOrdinaryMethod"
          border
          style="width: 100%; margin-top: 20px">
          <el-table-column  prop="ordStatus"  label="	委托状态"></el-table-column>
          <el-table-column  prop="sendingTime"  label="发送的北京时间"></el-table-column>
          <el-table-column  prop="clOrdId"  label="委托编号"></el-table-column>
          <el-table-column  prop="clientId"  label="母账户编号"></el-table-column>
          <el-table-column  prop="handlInst"  label="委托级别"></el-table-column>
          <el-table-column  prop="symbol"  label="六位证券代码"></el-table-column>
          <el-table-column  prop="securityType"  label="证券类型"></el-table-column>
          <el-table-column  prop="securityExchange"  label="交易所类型"></el-table-column>
          <el-table-column  prop="side"  label="买/卖"></el-table-column>
          <el-table-column  prop="transactTime"  label="委托发送的北京时间"></el-table-column>
          <el-table-column  prop="orderQty"  label="委托股数"></el-table-column>
          <el-table-column  prop="ordType"  label="委托类型"></el-table-column>
          <el-table-column  prop="price"  label="委托价格"></el-table-column>
          <el-table-column  prop="cumQty"  label="成交数量"></el-table-column>
          <el-table-column  prop="leavesQty"  label="仍可撮合量"></el-table-column>
          <el-table-column  prop="avgPx"  label="成交均价"></el-table-column>
          <el-table-column fixed="right" width="100"  label="操作">
            <template slot-scope="scope">
              <!-- 添加新机构用户 -->
              <el-button type="danger" @click="comOrdinaryUndo(scope.row)">{{txtsMsg}}</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
</template>

<script>
import axios from "axios";
import common from "@/utils/common"
export default {
	// 注入reload方法刷新页面
  inject:['reload'],
  name: "commonOrdinary",
  data() {
    return {
      txtsMsg:'撤单',
      commonOrdinaryToken:'',
      commonOrdinaryUserID:common.decipher('wsUserID'),
      comOrdinaryOptions:[],
      comOrdinaryClientID:'',//账户ClientID
      comOrdinaryClientName:'',//账户ClientName
      comOrdinarySymbol:'',//六位证券代码
      comOrdinaryOrderQty:'',//委托股数
      comOrdinaryPrice:'',//委托价格
      comOrdinarySide:'',//买/卖
      comOrdinaryTableData:[],//回报列表
      websock:'',
      responseDatas:[],
    };
  },
  mounted:function(){
    // 获取产品名
    this.comOrdinaryPro();
    // 获取回报列表
    this.getReturnActual();
    // 调websocket,获取回报列表
    this.websocketInit()
    setTimeout(this.readyChat('userId_'+this.commonOrdinaryUserID),5000)
  },
  methods: {
		// 获取回报
		getReturnActual(){
      this.commonOrdinaryUserID = common.decipher('wsUserID');
			console.log(domain.comOrdinaryReturnActualUser+this.commonOrdinaryUserID)
			axios.get(domain.comOrdinaryReturnActualUser+this.commonOrdinaryUserID)
				.then((response) => {
					console.log(response)
					if (common.isNull(response.data.responseEntity)) {
            common.altPrompt('暂无回报')
						this.comOrdinaryTableData = [];
					} else {
            // for循环找到时间戳，转化时间格式
						for (var i = 0; i < response.data.responseEntity.length; i++) {
              // 委托状态，0已报，1部成，2已成，4已撤，6待撤，8拒绝，10未报
              if (response.data.responseEntity[i].ordStatus == '0') {
                response.data.responseEntity[i].ordStatus = '已报'
              } else if (response.data.responseEntity[i].ordStatus == '1') {
                response.data.responseEntity[i].ordStatus = '部成'
              } else if (response.data.responseEntity[i].ordStatus == '2') {
                response.data.responseEntity[i].ordStatus = '已成'
              } else if (response.data.responseEntity[i].ordStatus == '4') {
                response.data.responseEntity[i].ordStatus = '已撤'
              } else if (response.data.responseEntity[i].ordStatus == '6') {
                response.data.responseEntity[i].ordStatus = '待撤'
              } else if (response.data.responseEntity[i].ordStatus == '8') {
                response.data.responseEntity[i].ordStatus = '拒绝'
              } else if (response.data.responseEntity[i].ordStatus == '10') {
                response.data.responseEntity[i].ordStatus = '未报'
              }
              response.data.responseEntity[i].sendingTime = common.changeTime(response.data.responseEntity[i].sendingTime)
              response.data.responseEntity[i].transactTime = common.changeTime(response.data.responseEntity[i].transactTime)
              // 证券类型,0 CS,13 RB,15 ETF
              if(response.data.responseEntity[i].securityType == '0'){
                response.data.responseEntity[i].securityType = 'CS'
              }else if(response.data.responseEntity[i].securityType == '13'){
                response.data.responseEntity[i].securityType = 'RB'
              }else if(response.data.responseEntity[i].securityType == '15'){
                response.data.responseEntity[i].securityType = 'ETF'
              }
              // 1买，2卖
              if(response.data.responseEntity[i].side == '1'){
                response.data.responseEntity[i].side = '买'
              }else if(response.data.responseEntity[i].side == '2'){
                response.data.responseEntity[i].side = '卖'
              }
              // 交易所类型,1深圳SZ，2上海SH
              if(response.data.responseEntity[i].securityExchange == '1'){
                response.data.responseEntity[i].securityExchange = 'SZ'
              }else if(response.data.responseEntity[i].securityExchange == '2'){
                response.data.responseEntity[i].securityExchange = 'SH'
              }
              // 委托类型，0限价委托，1信用委托
              if(response.data.responseEntity[i].ordType == '0'){
                response.data.responseEntity[i].ordType = '限价委托'
              }else if(response.data.responseEntity[i].ordType == '1'){
                response.data.responseEntity[i].ordType = '信用委托'
              }
              // 六位证券代码小于六位加00
              let a = '';
              a += response.data.responseEntity[i].symbol
              if (a.length == 0) {
                response.data.responseEntity[i].symbol = '000000'
              }else if (a.length == 1) {
                response.data.responseEntity[i].symbol = '00000'+a
              }else if(a.length == 2){
                response.data.responseEntity[i].symbol = '0000'+a
              }else if(a.length == 3){
                response.data.responseEntity[i].symbol = '000'+a
              }else if(a.length == 4){
                response.data.responseEntity[i].symbol = '00'+a
              }else if(a.length == 5){
                response.data.responseEntity[i].symbol = '0'+a
              }
						}
						this.comOrdinaryTableData = response.data.responseEntity;
					}
				})
				.catch((error) => {
					console.log(error)
        })
    },
    // 实际调取websocket会话函数
		readyChat(data){
      // 1_clientId和2_userId
      let that = this;
      console.log(data,this.websock.readyState)
      if (this.websock.readyState === 1) {//链接成功
        console.log('链接成功readyState === 1')
				this.websocketSend(data)
      } else if(this.websock.readyState === 0){//正在链接，300ms后发送信息
        console.log('正在链接')
				setTimeout(that.websocketSend(data),300)
			}else{//未链接或链接失败,初始化然后500ms后发送信息
        console.log('链接失败')
        this.websocketInit();
				setTimeout(that.websocketSend(data),500)
			}
		},
		// 初始化websocket
		websocketInit(){
      console.log(domain.authBaseWebsocketOrdinary)
			this.websock = new WebSocket(domain.authBaseWebsocketOrdinary);
			this.websock.onmessage = this.websocketMessage;
		},
		// websocket发送信息
		websocketSend(data){
      console.log(data,'发送消息')
			this.websock.send(data)
		},
		// webscoket接收服务端返回的信息
		websocketMessage(e){
      console.log(e)
      if (common.isNull(e)) {
        common.altPrompt('暂无回报')
				this.comOrdinaryTableData = [];
      } else {
        this.responseDatas = JSON.parse(e.data)
        console.log(this.responseDatas,this.responseDatas.clOrdId)
        // 委托状态，0已报，1部成，2已成，4已撤，6待撤，8拒绝，10未报
        if (this.responseDatas.ordStatus == '0') {
          this.responseDatas.ordStatus = '已报'
        } else if (this.responseDatas.ordStatus == '1') {
          this.responseDatas.ordStatus = '部成'
        } else if (this.responseDatas.ordStatus == '2') {
          this.responseDatas.ordStatus = '已成'//
        } else if (this.responseDatas.ordStatus == '4') {
          this.responseDatas.ordStatus = '已撤'//
        } else if (this.responseDatas.ordStatus == '6') {
          this.responseDatas.ordStatus = '待撤'//
        } else if (this.responseDatas.ordStatus == '8') {
          this.responseDatas.ordStatus = '拒绝'
        } else if (this.responseDatas.ordStatus == '10') {
          this.responseDatas.ordStatus = '未报'
        }
        for (let i = 0; i < this.comOrdinaryTableData.length; i++) {
          if (this.responseDatas.clOrdId == this.comOrdinaryTableData[i].clOrdId) {
            this.comOrdinaryTableData[i].ordStatus = this.responseDatas.ordStatus
          }
          
        }
      }
    },
    // 关闭websocket
    websockclose(e){
      console.log("close"+e)
    },
    // 获取账户名
    comOrdinaryPro: function() {
        this.commonOrdinaryUserID = common.decipher('wsUserID');
        console.log(domain.indFindCustomer+this.commonOrdinaryUserID)
				axios
        .get(domain.indFindCustomer+this.commonOrdinaryUserID)
        .then((response) => {
          console.log(response)
          this.comOrdinaryOptions = response.data.responseEntity;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 根据账户名展示单个回报
		handleClickcomOrdinary(){
			// for循环找出账户名对应的clientid
			for (var i = 0; i < this.comOrdinaryOptions.length; i++) {
				if(this.comOrdinaryClientName == this.comOrdinaryOptions[i].clientName){
					this.commonOrdinaryClientID = this.comOrdinaryOptions[i].clientID
				}
      }
      
      // 调websocket
      this.readyChat('clientId_'+this.commonOrdinaryClientID)
      console.log(this.responseDatas,this.responseDatas.clOrdId)

			console.log(domain.comOrdinaryReturnActualClientId+this.commonOrdinaryClientID)
			axios.get(domain.comOrdinaryReturnActualClientId+this.commonOrdinaryClientID)
				.then((response) => {
					console.log(response,common.getNowFormatDate())
					if (common.isNull(response.data.responseEntity)) {
            common.altPrompt('暂无回报')
						this.comOrdinaryTableData = [];
					} else{
            // for循环找到时间戳，转化时间格式
						for (var k = 0; k < response.data.responseEntity.length; k++) {
              // 委托状态，0已报，1部成，2已成，4已撤，6待撤，8拒绝，10未报
              if (response.data.responseEntity[k].ordStatus == '0') {
                response.data.responseEntity[k].ordStatus = '已报'
              } else if (response.data.responseEntity[k].ordStatus == '1') {
                response.data.responseEntity[k].ordStatus = '部成'
              } else if (response.data.responseEntity[k].ordStatus == '2') {
                response.data.responseEntity[k].ordStatus = '已成'//
              } else if (response.data.responseEntity[k].ordStatus == '4') {
                response.data.responseEntity[k].ordStatus = '已撤'//
              } else if (response.data.responseEntity[k].ordStatus == '6') {
                response.data.responseEntity[k].ordStatus = '待撤'//
              } else if (response.data.responseEntity[k].ordStatus == '8') {
                response.data.responseEntity[k].ordStatus = '拒绝'
              } else if (response.data.responseEntity[k].ordStatus == '10') {
                response.data.responseEntity[k].ordStatus = '未报'
              }

              response.data.responseEntity[k].sendingTime = common.changeTime(response.data.responseEntity[k].sendingTime)
              response.data.responseEntity[k].transactTime = common.changeTime(response.data.responseEntity[k].transactTime)
              // 证券类型,0 CS,13 RB,15 ETF
              if(response.data.responseEntity[k].securityType == '0'){
                response.data.responseEntity[k].securityType = 'CS'
              }else if(response.data.responseEntity[k].securityType == '13'){
                response.data.responseEntity[k].securityType = 'RB'
              }else if(response.data.responseEntity[k].securityType == '15'){
                response.data.responseEntity[k].securityType = 'ETF'
              }
              // 1买，2卖
              if(response.data.responseEntity[k].side == '1'){
                response.data.responseEntity[k].side = '买'
              }else if(response.data.responseEntity[k].side == '2'){
                response.data.responseEntity[k].side = '卖'
              }
              // 交易所类型,1深圳SZ，2上海SH
              if(response.data.responseEntity[k].securityExchange == '1'){
                response.data.responseEntity[k].securityExchange = 'SZ'
              }else if(response.data.responseEntity[k].securityExchange == '2'){
                response.data.responseEntity[k].securityExchange = 'SH'
              }
              // 委托类型，0限价委托，1信用委托
              if(response.data.responseEntity[k].ordType == '0'){
                response.data.responseEntity[k].ordType = '限价委托'
              }else if(response.data.responseEntity[k].ordType == '1'){
                response.data.responseEntity[k].ordType = '信用委托'
              }
              // 六位证券代码小于六位加0
              let a = '';
              a += response.data.responseEntity[k].symbol
              if (a.length == 0) {
                response.data.responseEntity[k].symbol = '000000'
              }else if (a.length == 1) {
                response.data.responseEntity[k].symbol = '00000'+a
              }else if(a.length == 2){
                response.data.responseEntity[k].symbol = '0000'+a
              }else if(a.length == 3){
                response.data.responseEntity[k].symbol = '000'+a
              }else if(a.length == 4){
                response.data.responseEntity[k].symbol = '00'+a
              }else if(a.length == 5){
                response.data.responseEntity[k].symbol = '0'+a
              }
						}
            this.comOrdinaryTableData = response.data.responseEntity;
					}
					
				})
				.catch((error) => {
					console.log(error)
        })
		},
    // 点击下单按钮
    sureOrder(){
      // for循环找到产品对应的clientID
      for (let i = 0; i < this.comOrdinaryOptions.length; i++) {
        if (this.comOrdinaryClientName == this.comOrdinaryOptions[i].clientName) {
          this.comOrdinaryClientID = this.comOrdinaryOptions[i].clientID;
        }
      }

			let comOrdinarySymbolReg = /^[0-9]\d*$/;//股票代码正则验证
			// let comOrdinaryPriceReg = /^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$/;
			let comOrdinaryPriceReg = /^[0-9]+(.[0-9]{2})?$/;//小数点后二位
      let comOrdinarySymbolCD1 = this.comOrdinarySymbol.substr(0,1);
      let comOrdinarySymbolCD2 = this.comOrdinarySymbol.substr(0,2);
      console.log(comOrdinarySymbolCD1,comOrdinarySymbolCD2,this.comOrdinarySymbol,this.comOrdinaryOrderQty.substr(0,1))

      if(common.isNull(this.comOrdinaryClientName)){
        common.altPrompt('产品名不能为空')
        return false;
      }

      if((this.comOrdinarySymbol.length == '6') && (comOrdinarySymbolReg.test(this.comOrdinarySymbol)) && ((comOrdinarySymbolCD2 == '00') || (comOrdinarySymbolCD2 == '30') || (comOrdinarySymbolCD2 == '15') || (comOrdinarySymbolCD2 == '13') || (comOrdinarySymbolCD1 == '2') || (comOrdinarySymbolCD1 == '6'))){
        console.log('股票代码正确')
      }else{
        common.altPrompt('股票代码错误');
        return false;
      }

      if(comOrdinarySymbolReg.test(this.comOrdinaryOrderQty) && this.comOrdinaryOrderQty.substr(0,1) != 0 && common.goAliquot(this.comOrdinaryOrderQty) != false){
        console.log('股数正确')
      }else{
        common.altPrompt('股数错误');
        return false;
      }

      if(common.isNull(this.comOrdinaryPrice) || this.comOrdinaryPrice == 0){
        common.altPrompt('股价不能为空或零');
        return false;
      }else if(comOrdinarySymbolReg.test(this.comOrdinaryPrice) && this.comOrdinaryPrice.substr(0,1) != 0 || comOrdinaryPriceReg.test(this.comOrdinaryPrice)){
        console.log('股价正确')
      }else{
        common.altPrompt('股价错误');
        return false;
      }

      if (common.isNull(this.comOrdinarySide)) {
        common.altPrompt('请选择买卖方向')
        return false;
      }
      if (this.comOrdinarySide == '买') {
        this.comOrdinarySide = '1'
      } else if(this.comOrdinarySide == '卖'){
        this.comOrdinarySide = '2'
      }
      console.log(domain.comOrdinaryNewActualOrder,this.comOrdinaryClientID,this.comOrdinarySymbol,this.comOrdinarySide,this.comOrdinaryOrderQty,this.comOrdinaryPrice,)
      // 下单
      axios.post(domain.comOrdinaryNewActualOrder,{
        ClientID:this.comOrdinaryClientID,
        Symbol:this.comOrdinarySymbol,
        Side:this.comOrdinarySide,
        OrderQty:this.comOrdinaryOrderQty,
        Price:this.comOrdinaryPrice,
      }).then((response) => {
        console.log(response)
        if (response.data.responseEntity == 'OK') {
          this.$message({
          	type: 'success',
          	message: '下单成功'
          })
          this.comOrdinarySymbol = '';
          this.comOrdinarySide = '';
          this.comOrdinaryOrderQty = '';
          this.comOrdinaryPrice = '';
          // setInterval(this.handleClickcomOrdinary,500)
          this.handleClickcomOrdinary();//调用刷新方法
        }else{
					common.altPrompt('下单失败')
					this.comOrdinarySymbol = '';
					this.comOrdinarySide = '';
					this.comOrdinaryOrderQty = '';
          this.comOrdinaryPrice = '';
          this.handleClickcomOrdinary();//调用刷新方法
				}
      }).catch((error) => {
        console.log(error)
      })
    },
    // 撤单
    comOrdinaryUndo(row,index){
      console.log(row,this.comOrdinaryClientName,domain.comOrdinaryCancelActualOrder+row.clOrdId+'/'+row.clientId)
      // 委托状态，0已报，1部成，2已成，4已撤，6待撤，8拒绝，10未报
      if (row.ordStatus == '已报') {
        console.log('已报')
      }else if(row.ordStatus == '部成'){
        console.log('部成')
      }else if(row.ordStatus == '已成'){
        common.altPrompt('已成交，无法撤单')
        return false
      }else if(row.ordStatus == '已撤'){
        common.altPrompt('已撤单，请勿重复点击')
        return false
      }else if(row.ordStatus == '待撤'){
        console.log('待撤')
      }else if(row.ordStatus == '拒绝'){
        common.altPrompt('已拒绝下单，无法撤单')
        return false
      }else if(row.ordStatus == '未报'){
        console.log('未报')
      }
      // 
      this.$confirm('此操作将撤销此单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // 调撤单接口
          axios.get(domain.comOrdinaryCancelActualOrder+row.clOrdId+'/'+row.clientId)
            .then((response) => {
              console.log(response)
              // 成功重新调取回报接口
              if (response.data.responseEntity == 'OK') {
                common.altPrompt('撤单成功')
                if (common.isNull(this.comOrdinaryClientName)) {
                  this.reload()
                } else {
                  this.handleClickcomOrdinary()
                }
              } else{
                common.altPrompt('撤单失败')
              }
            })
            .catch((error) => {
              console.log(error)
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消撤单'
          });          
        });
			
    },
		// 获取行列信息
		comOrdinaryMethod({ row, column, rowIndex, columnIndex }) {
			// console.log(row,column,rowIndex,columnIndex)
		},
  }
};
</script>

<style>
</style>



